import { request,getToken,Token } from '../request.js'
import Loading from '../common/loading/loading.vue'
export default {
	data() {
		return {
			is_parter:0,
			statusHeight: '',
			baseURL:this.$api,
			isLogin:true,
			user_name:'未登陆',
			avatar:'',
			ordernum:{
				0:0,
				1:0,
				2:0
			},
			area:'',
			// 储存推广的uid
			uid: '',
			res: '',
			// 用户的余额信息
			user_balance: '',
			// 加载图
			loading: false,
			$src: this.$src
		};
	},
	components: {
		Loading
	},
	onShow() {
		this.getLogin();
		uni.getSystemInfo({
			success: res => {
				this.statusHeight = res.statusBarHeight + 'px'
			}
		})
		// 获取用户余额信息
		this.getUserMoney();
		

	},
	methods:{
		// 检测登录状态
		getLogin() {
			let that = this;
			request({
					url:"index.php?s=/wap/member/checkLogin",
					method:"POST",
				}).then(res=>{
					that.loading = true
					// 未登录
					if(res.data.code == 201) {
						this.isLogin = false
					}else{
						this.isLogin = true;
						// 获取用户信息
						that.getUserInfo();
					}
				});
		},
		// 获取用户个人信息
		getUserInfo(){
			let that = this;
			request({
					url:"index.php?s=/wap/member/Api_member_index",
					method:"POST",
				}).then(function(res){
					// 推广uid保存到本地
					uni.setStorage({
						key: 'uid',
						data: res.data.uid
					});
					that.loading = true;
					that.res = res;
					that.is_parter = res.data.is_parter;
					// 储存推广的uid
					that.uid = res.data.uid;
					that.user_name = res.data.user_name;
					that.avatar = res.data.member_img;
					that.ordernum = {
						0:res.data.unpaidOrder,
						1:res.data.shipmentPendingOrder,
						2:res.data.goodsNotReceivedOrder,
					};
					let address = res.data.address;
					that.area = address.province + " " + address.city + " " + address.district;
				});
		},
		// 获取用户余额信息
		getUserMoney() {
			request({
				url: 'index.php?s=/wap/member/getMomeyInfo'
			}).then(res => {
				this.user_balance = res.data.data;
			})
		},

		// 跳转到登录页
		login() {
			uni.navigateTo({
				url: '../components/login/login'
			})
		},
		// 跳转到注册页
		register() {
			uni.navigateTo({
				url:'../components/register/register'
			})
		},
		// 跳转到order我的订单页
		order(status) {
			uni.navigateTo({
				url: '/pages/components/order/order?status='+status
			})
		},
		// 跳转到收藏页
		collect() {
			uni.navigateTo({
				url: '/pages/components/collect/collect'
			})
		},
		// 跳到收货地址
		address() {
			uni.navigateTo({
				url:'/pages/components/address/address'
			})
		},
		// 跳转到我的钱包页面
		money() {
			uni.navigateTo({
				url: '/pages/components/money/money'
			})
		},
		// 跳转到商家入驻页面
		goBusiness() {
			uni.navigateTo({
				url: '/pages/components/business-nav/business-nav'
			})
		},
		// 跳转到商家管理页面
		goBusinessManage() {
			uni.navigateTo({
				url: '/pages/components/business-manage/business-manage'
			})
		},
		// 跳转到我的推广页面
		tuiguang() {
			uni.navigateTo({
				url: '/pages/components/generalize/generalize?uid='+this.uid
			})
		},
		// 跳转到个人中心页
		userInfo() {
			uni.navigateTo({
				url: '/pages/components/userInfo/userInfo'
			})
		},
		//跳转员工管理
		staff(){
			uni.navigateTo({
				// url: '/pages/mine/staff'
				url: '/pages/components/preson-status/preson-status'
			})
		},
		
		// 跳转到充值页面
		goPayMoney() {
			uni.navigateTo({
				url: '/pages/components/member/member'
			})
		},
		// 跳转到我的团队
		goGroup() {
			uni.navigateTo({
				url: '/pages/components/group/group'
			})
		}
	},
	
}
